window.addEventListener('DOMContentLoaded', () => {

   //!Tabs

   const wrapperTabs = document.querySelector('.tabheader__items'),
      tabs = wrapperTabs.querySelectorAll('.tabheader__item'),
      tabContent = document.querySelectorAll('.tabcontent');

   wrapperTabs.addEventListener('click', (event) => {
      event.preventDefault();

      const target = event.target;

      if (target && target.classList.contains('tabheader__item')) {
         removeClasses(tabs, 'tabheader__item_active');
         hideTabsContent(tabContent);

         tabs.forEach((item, idx) => {
            if (target == item) {
               showTabsContent(idx, tabContent)
            }
         });

         target.classList.add('tabheader__item_active');
      }
   });

   hideTabsContent(tabContent);
   showTabsContent();

   function hideTabsContent(items) {
      items.forEach(tab => {
         tab.style.display = 'none';
      })
   }

   function showTabsContent(id = 0, showContent = tabContent) {
      showContent[id].style.display = 'block';
   }

   function removeClasses(item, classRemove) {
      item.forEach(element => {
         element.classList.remove(classRemove);
      });
   }

   //!Timer

   const deadLine = '2022-07-10';

   setClock(deadLine, '.timer');

   function getTimeRemaining(endTime) {
      const t = Date.parse(endTime) - Date.parse(new Date()),
         days = Math.floor(t / (1000 * 60 * 60 * 24)),
         hours = Math.floor(t / (1000 * 60 * 60) % 24),
         minutes = Math.floor(t / (1000 * 60) % 60),
         seconds = Math.floor(t / (1000) % 60);

      return { t, days, hours, minutes, seconds };

   }

   function getZero(num) {
      return (num < 10 && num > 0) ? `0${num}` : num;
   }

   function setClock(endTime, selector) {
      const timer = document.querySelector(selector),
         days = timer.querySelector('#days'),
         hours = timer.querySelector('#hours'),
         minutes = timer.querySelector('#minutes'),
         seconds = timer.querySelector('#seconds'),
         timeInterval = setInterval(updateTime, 1000);

      updateTime();

      function updateTime() {
         const t = getTimeRemaining(endTime);

         days.textContent = getZero(t.days);
         hours.textContent = getZero(t.hours);
         minutes.textContent = getZero(t.minutes);
         seconds.textContent = getZero(t.seconds);

         if (t.t <= 0) {
            clearInterval(timeInterval);

            days.textContent = '0';
            hours.textContent = '0';
            minutes.textContent = '0';
            seconds.textContent = '0';
         }
      }
      updateTime();
   }

   //Modal

   const modal = document.querySelector('.modal'),
      btnOpenModal = document.querySelectorAll('[data-modal]'),
      modalClose = modal.querySelector('[data-close]');

   const modalTimerId = setTimeout(openModal, 10000);

   btnOpenModal.forEach(btn => {
      btn.addEventListener('click', (event) => {
         event.preventDefault();
         openModal();
         clearTimeout(modalTimerId);
      })
   });

   modal.addEventListener('click', event => {
      const target = event.target;

      if (target && target.classList.contains('modal') || target === modalClose) {
         closeModal();
      }
   });

   document.addEventListener('keydown', (event) => {
      if (event.code === 'Escape') closeModal();
   });

   window.addEventListener('scroll', showModalByScroll);

   function openModal() {
      modal.style.display = 'block';
      document.body.style.overflow = 'hidden';
   }

   function closeModal() {
      modal.style.display = 'none';
      document.body.style.overflow = '';
   }

   function showModalByScroll() {
      if (window.pageYOffset + document.documentElement.clientHeight >= document.documentElement.scrollHeight) {
         openModal();
         window.removeEventListener('scroll', showModalByScroll)
      }
   }

   class MenuItem {
      constructor(src, alt, title, decr, price, parentSelector, ...classes) {
         this.src = src;
         this.alt = alt;
         this.title = title;
         this.decr = decr;
         this.price = price;
         this.classes = classes
         this.transfer = 60;
         this.parent = document.querySelector(parentSelector);
         this.changeToRub();
      }

      changeToRub() {
         this.price = this.transfer * this.price;
      }

      render() {
         const element = document.createElement('div');
         console.log(this);
         if (this.classes.length === 0) {
            this.classes = 'menu__item'
            element.classList.add(this.classes);
         } else {
            this.classes.forEach(className => element.classList.add(className));
         }

         element.innerHTML = `
         <img src="${this.src}" alt="${this.alt}">
         <h3 class="menu__item-subtitle">Меню "${this.title}"</h3>
         <div class="menu__item-descr">${this.decr} </div>
         <div class="menu__item-divider"></div>
         <div class="menu__item-price">
            <div class="menu__item-cost">Цена:</div>
            <div class="menu__item-total"><span>${this.price}</span> руб/день</div>
         </div>
         `
         this.parent.append(element);
      }
   }

   new MenuItem(
      'img/tabs/vegy.jpg',
      'vegy',
      'Фитнес',
      'Меню "Фитнес" - это новый подход к приготовлению блюд: больше свежих овощей и фруктов.Продукт активных и здоровых людей.Это абсолютно новый продукт с оптимальной ценой и высоким качеством!',
      500,
      '.menu__field .container',
      'menu__item',
      'big'
   ).render();

   new MenuItem(
      'img/tabs/vegy.jpg',
      'vegy',
      'Фитнес',
      'Меню "Фитнес" - это новый подход к приготовлению блюд: больше свежих овощей и фруктов.Продукт активных и здоровых людей.Это абсолютно новый продукт с оптимальной ценой и высоким качеством!',
      10,
      '.menu__field .container',
      'menu__item'
   ).render();

   new MenuItem(
      'img/tabs/elite.jpg',
      'elite',
      '“Премиум”',
      'Меню "Премиум" - это новый подход к приготовлению блюд: больше свежих овощей и фруктов.Продукт активных и здоровых людей.Это абсолютно новый продукт с оптимальной ценой и высоким качеством!',
      30,
      '.menu__field .container'
   ).render();
});